.nosotros-main {
  margin: 0px 0px;
  padding: 30px 0;
}

@media screen and (min-width: 768px) {
  .nosotros-main {
    margin: 0px 0px;
    background: url(../img/about-index-img.png) no-repeat;
    background-size: 100%;
    background-position: center 70%;
  }
}

@media screen and (min-width: 1024px) {
  .nosotros-main {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-column-gap: 2em;
    background-size: 60%;
    background-position: center left;
    padding: 50px 0;
  }
}

.nosotros-main__info {
  text-align: left;
  padding: 20px 0px;
}

@media screen and (min-width: 768px) {
  .nosotros-main__info {
    background-size: 40%;
  }
}

@media screen and (min-width: 1024px) {
  .nosotros-main__info {
    text-align: left;
    background-size: 90%;
  }
}

.nosotros-main__info h3 {
  font-family: "bold", sans-serif;
  color: #c0aa83;
  font-weight: 700;
  font-size: 1.2em;
}

@media screen and (min-width: 768px) {
  .nosotros-main__info h3 {
    font-size: 2em;
  }
}

.nosotros-main__info h1 {
  font-family: "bold", sans-serif;
  color: #242424;
  font-weight: 700;
  font-size: 2em;
}

@media screen and (min-width: 768px) {
  .nosotros-main__info h1 {
    font-size: 3.5em;
  }
}

.nosotros-main__info p {
  width: 100%;
  font-family: "regular", sans-serif;
  font-size: 1em;
  font-weight: 400;
  line-height: 30px;
  color: #242424;
  margin: 40px 0;
}

@media screen and (min-width: 768px) {
  .nosotros-main__info p {
    width: 80%;
    font-size: 1.3em;
    line-height: 36px;
  }
}

.nosotros-main__info span {
  width: 100%;
  text-align: center;
  font-family: "bold", sans-serif;
  color: #242424;
  font-size: 1.5em;
}

.nosotros-main__service__items {
  margin: 20px 0;
  text-align: left;
}

@media screen and (min-width: 768px) {
  .nosotros-main__service__items {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-column-gap: 2em;
    text-align: left;
  }
}

.nosotros-main__service__items__item {
  margin-top: 50px;
}

@media screen and (min-width: 768px) {
  .nosotros-main__service__items__item {
    margin-top: 0px;
  }
}

.nosotros-main__service__items__item h2 {
  font-family: "bold", sans-serif;
  color: #242424;
  font-weight: 700;
  font-size: 1.1em;
  margin: 15px 0;
}

.nosotros-main__service__items__item p {
  font-family: "light", sans-serif;
  color: #242424;
  font-size: 1em;
  margin: 0px;
}

@media screen and (min-width: 768px) {
  .nosotros-main__service__items__item p {
    font-size: 0.9em;
  }
}

@media screen and (min-width: 768px) {
  .productos-main__blocktitle {
    height: 200px;
  }
}

.productos-main__blocktitle__title {
  text-align: center;
}

.productos-main__blocktitle__title span {
  font-family: "bold", sans-serif;
  color: #f4f4f4;
  opacity: 0.4;
  font-weight: 700;
  font-size: 6em;
  display: none;
}

@media screen and (min-width: 768px) {
  .productos-main__blocktitle__title span {
    position: absolute;
    left: 0;
    right: 0;
    display: block;
  }
}

@media screen and (min-width: 1024px) {
  .productos-main__blocktitle__title span {
    font-size: 7em;
  }
}

.productos-main__blocktitle__title h1 {
  font-family: "bold", sans-serif;
  color: #44200e;
  font-weight: 700;
  font-size: 3em;
}

@media screen and (min-width: 768px) {
  .productos-main__blocktitle__title h1 {
    position: relative;
    top: 40px;
  }
}

@media screen and (min-width: 1024px) {
  .productos-main__blocktitle__title h1 {
    font-size: 4em;
    top: 50px;
  }
}

@media screen and (min-width: 1024px) {
  .productos-main__blockproducts {
    display: grid;
    grid-template-columns: 1fr 1fr;
  }
}

.productos-main__blockproducts__item {
  max-width: 100%;
}

.productos-main__blockproducts__item h2 {
  font-family: "bold", sans-serif;
  color: #c0aa83;
  font-weight: 700;
  font-size: 1.5em;
}

@media screen and (min-width: 1024px) {
  .productos-main__blockproducts__item {
    text-align: center;
  }
}

@media screen and (min-width: 768px) {
  .productos-main__blockproducts__item__items {
    display: grid;
    grid-template-columns: 1fr 1fr;
    padding: 40px 0;
  }
}

.productos-main__blockproducts__item__items div {
  text-align: center;
}

.productos-main__blockproducts__item__items div h3 {
  font-family: "bold", sans-serif;
  color: #44200e;
  font-weight: 500;
  font-size: 1.2em;
}

.productos-main__blockproducts__item__items div p {
  font-family: "light", sans-serif;
  color: "regular";
  font-weight: 600;
  font-size: 1em;
}

.l-container {
  width: 90%;
  max-width: 1300px;
  margin-left: auto;
  margin-right: auto;
}
