@font-face {
  font-family: 'icomoon';
  src:  url('../icon/icomoon.eot?wgwy6n');
  src:  url('../icon/icomoon.eot?wgwy6n#iefix') format('embedded-opentype'),
    url('../icon/icomoon.ttf?wgwy6n') format('truetype'),
    url('../icon/icomoon.woff?wgwy6n') format('woff'),
    url('../icon/icomoon.svg?wgwy6n#icomoon') format('svg');
  font-weight: normal;
  font-style: normal;
}

[class^="icon-"], [class*=" icon-"] {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: 'icomoon' !important;
  speak: none;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;

  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

